import {Component, OnInit, Input} from '@angular/core';
import {CheckBox, FieldBase, TextArea, Textbox} from '../../components/dynamic-form/form-field';
import {FormGroup, FormControl} from '@angular/forms';
import {PeopleService} from '../../services/people.service';
import {People} from '../../../entity/tree.entity';
import {FormService} from '../../services/form.service';

@Component({
  selector: 'app-people-manager',
  templateUrl: './people-manager.component.html',
  styleUrls: ['./people-manager.component.css']
})
export class PeopleManagerComponent implements OnInit {
  protected people: People;
  @Input() fields: FieldBase<any>[] = [
    new Textbox({
      label: '姓名(*):',
      placeholder: '姓名',
      key: 'name'
    }),
    new Textbox({
      label: '生日(*):',
      placeholder: '生日',
      key: 'birth_death',
      type: 'date'
    }),
    new TextArea({
      key: 'desc',
      label: '个人简介(*):',
      placeholder: '个人简介，最多140字，不能放链接。',
      rows: 3,
    }),
    new CheckBox({
      key: 'invalid',
      label: '',
      type: 'checkbox',
      options: [
        {id: '1', text: '发送邀请链接'}
      ]
    }),
    new Textbox({
      label: '性别:',
      placeholder: '性别',
      key: 'sex',
    }),
    new Textbox({
      key: 'email',
      type: 'email',
      label: '常用邮箱:',
      placeholder: '常用邮箱'
    }),
    new Textbox({
      label: '电话:',
      placeholder: '电话',
      key: 'phone',
    }),
  ];

  public form: FormGroup;

  constructor(private peopleService: PeopleService, private fromService: FormService) {
  }

  ngOnInit() {
    this.form = this.fromService.toFormGroup(this.fields);
    // this.getProfile();
    // this.activeRoute.params.subscribe(
    //   params => {
    //     console.log(params);
    //   }
    // );
  }

  save(e) {
    const profile = this.form.value as People;
    console.log(profile);
    this.peopleService.createPeople(profile).then(data => {
      if (data) {
        this.people = this.peopleService.people;
        alert('create ok');
      }
      else {
      }
    })
    ;
  }
}



