import {Component, OnInit, Input} from '@angular/core';
import {FieldBase, Textbox, TextArea} from '../components/dynamic-form/form-field';
import {FormGroup} from '@angular/forms';
import {CommentEntity} from '../../entity/comment.entity';
import {FormService} from '../services/form.service';
import {CommentService} from '../services/comment.service';

@Component({
  selector: 'app-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.css']
})
export class FeedbackComponent implements OnInit {
  @Input() fields: FieldBase<any>[];
  private form: FormGroup;

  constructor(private formService: FormService, private commentService: CommentService) {
  }

  ngOnInit() {
    this.fields = this.commentService.getFields();
    this.form = this.formService.toFormGroup(this.fields);
  }

  comment() {
    const comment = this.form.value as CommentEntity;
    comment.type = 'feedback';
    console.log(comment);
    this.commentService.comment(comment).then(ret => {
        if (ret) {
          alert('反馈成功，谢谢您的意见');
        } else {
          console.log(this.commentService.message);
          alert(this.commentService.message);
        }
      }
    );
  }
}



