import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {User} from '../../entity/user.entity';
import {Router} from '@angular/router';
import {local} from '../../../../../../../../Library/Preferences/WebStorm2017.3/javascript/extLibs/global-types/node_modules/@types/d3-selection';

@Injectable()
export class LoginService {
  private loginUrl = '/api/auth/login';
  private registerUrl = '/api/auth/signup';
  public headers = new HttpHeaders({
    'Content-Type': 'application/json'
  });
  private token: string;
  public isLogin = false;
  public message: string;

  constructor(private http: HttpClient, private router: Router) {
    if (localStorage.getItem('isLogin') === 'true') {
      this.isLogin = true;
      this.token = localStorage.getItem('token');
      console.log(this.token);
    }
  }

  login(user: User) {
    return this.http.post(this.loginUrl, JSON.stringify(user), {headers: this.headers}).toPromise().then(
      data => {
        console.log(this.isLogin);
        if (data['status'] === 'ok') {
          this.isLogin = true;
          this.token = data['token'];
          localStorage.setItem('token', this.token);
          localStorage.setItem('isLogin', 'true');
          return true;
        }
        this.message = data['message'];
        return false;
      }
    );
  }

  register(user: User) {
    if (user.username === '' || !user.username) {
      user.username = user.email;
    }
    return this.http.post(this.registerUrl, JSON.stringify(user), {headers: this.headers}).toPromise().then(
      data => {
        console.log(data);
        if (data['status'] === 'ok') {
          return true;
        }
        this.message = data['message'];
        return false;
      }
    ).catch(data => {
      console.log(data.error);
      if (data.error) {
        this.message = data.message;
      }
      return false;
    });
  }

  logout() {
    localStorage.removeItem('isLogin');
    console.log('logout');
    this.router.navigateByUrl('/');
  }

  getLoginedHeader() {
    if (this.isLogin) {
      return new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': 'Token '.concat(this.token)
      });
    } else {
      this.router.navigate(['/login']);
    }
  }

  cacheUser(user: User) {
    localStorage.setItem('user', JSON.stringify(user));
  }

  getCacheUser() {
    return JSON.parse(localStorage.getItem('user'));
  }
}



