import {Component, OnInit, Input} from '@angular/core';
import {User} from '../../../entity/user.entity';
import {LoginService} from '../../services/login.service';
import {Router} from '@angular/router';
import {FieldBase, Textbox} from '../../components/dynamic-form/form-field';
import {FormControl, FormGroup} from '@angular/forms';
import {FormService} from '../../services/form.service';


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  private user = new User();
  @Input() fields: FieldBase<any>[] = [
    new Textbox({
      label: '用户名:',
      placeholder: '用户名',
      key: 'username',
      required: true,
    }),
    new Textbox({
      label: '邮箱:',
      placeholder: '邮箱',
      key: 'email',
      required: true,
    }),
    new Textbox({
      label: '密 码:',
      placeholder: '密 码',
      key: 'password',
      type: 'password',
      required: true,
      validateEqual: 'repeat',
    }),
    new Textbox({
      key: 'repeat',
      type: 'password',
      label: '确认密码:',
      placeholder: '确认密码',
      required: true,
      validateEqual: 'password',
    })
  ];
  private form: FormGroup;

  constructor(private loginService: LoginService, private router: Router, private formService: FormService) {
  }

  ngOnInit() {
    if (this.loginService.isLogin) {
      this.router.navigateByUrl('/me');
    }
    this.form = this.formService.toFormGroup(this.fields);
  }

  save() {
    if (this.form.status === 'VALID') {
      this.loginService.register(this.form.value as User).then(
        data => {
          if (data) {
            console.log(data);
            this.router.navigateByUrl('/login');
          } else {
            console.log(this.loginService.message);
          }
        }
      );
    }
  }
}



