import {Injectable} from '@angular/core';
import {People} from '../../entity/tree.entity';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {LoginService} from './login.service';

@Injectable()
export class PeopleService {
  public people: People;
  public peoples: [[People]];
  private peopleUrl = '/api/people/';
  private firstUrl = '/api/people/first';
  private searchUrl = '/api/people/query';
  private headers = new HttpHeaders({
    'Content-Type': 'application/json'
  });
  private cacheStr = 'rootPeople';
  private previous = '';
  private next = '';
  public message: string;

  constructor(private http: HttpClient, private loginService: LoginService) {
  }

  /**
   * 获取具体的人
   * @param {number} id
   * @returns {Promise<People>}
   */
  getData(id: number = 1) {
    return this.http.get(`${this.peopleUrl}/${id}`, {headers: this.headers})
      .toPromise()
      .then(data => {
        this.people = data['results'] as People;
        return this.people;
      });
  }

  /**
   * 获取第一个人
   * @param {number} id
   * @returns {Promise<People>}
   */
  getFirstData() {
    return this.http.get(this.firstUrl, {headers: this.headers})
      .toPromise()
      .then(data => {
        this.people = data['results'] as People;
        return this.people;
      });
  }

  /**
   * 创建传人
   */
  createPeople(people: People) {
    return this.http.post(`${this.peopleUrl}`, JSON.stringify(people), {headers: this.loginService.getLoginedHeader()})
      .toPromise()
      .then(data => {
        this.people = data['results'] as People;
        return true;
      }).catch(data => {
        this.message = data.message;
        return false;
      });
  }

  /**
   * 搜索
   * @param {string} title
   * @param {page} 分页
   */
  queryData(title: string, page) {
    const params = {
      name: title
    };
    if (page) {
      params['page'] = page;
    }
    return this.http.get(`${this.searchUrl}`, {headers: this.headers, params: params})
      .toPromise()
      .then(data => {
        if (data['status'] === 'ok') {
          this.peoples = data['results'] as [[People]];
          this.previous = this.getPage(data['previous']);
          this.next = this.getPage(data['next']);
          console.log(this.next);
          return true;
        } else {
          this.message = data['message'];
          return false;
        }
      }).catch(data => {
        this.message = data.message;
        return false;
      });
  }

  getPage(url: string) {
    if (url && url !== null) {
      const pages = url.match('(?<=page=)\\d');
      if (pages) {
        return pages[0];
      }
      return '1';
    }
  }

  cacheData(people: People) {
    let root = localStorage.getItem(this.cacheStr);
    if (!root) {
      root = JSON.stringify(people);
      localStorage.setItem(this.cacheStr, root);
      return;
    }
    const rootPeople = JSON.parse(root);
    rootPeople.childes.forEach((p, index) => {
      if (p.id === people.id) {
        rootPeople.childes[index] = people;
      }
    });
  }

  getCacheData() {
    const data = localStorage.getItem(this.cacheStr);
    if (data) {
      return JSON.parse(data);
    }
  }
}



