/**
 * 用户信息
 */
import {People} from './tree.entity';

export class User {
  id: number;
  username: string;
  email: string;
  password: string;
}

/**
 * 用户附加信息
 */
export class UserProfile {
  user: User;
  nick: string;
  phone: string;
  sex: string;
  birth: string;
  avatar: string;
  people: People;
}



