import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';
import {LoginService} from './login.service';
import {CommentEntity} from '../../entity/comment.entity';
import {FieldBase, TextArea, Textbox} from '../components/dynamic-form/form-field';

@Injectable()
export class CommentService {
  private commentUrl = '/api/comments/';

  public message: string;
  public comments: [CommentEntity];

  constructor(private http: HttpClient, private router: Router, private loginService: LoginService) {
  }

  getFields() {
    let fields: FieldBase<any>[] = [
      new TextArea({
        label: '意见:',
        placeholder: '您的意见',
        key: 'comment',
        rows: 3,
        required: true,
      })
    ];
    if (this.loginService.isLogin) {
    } else {
      fields = fields.concat([
        new Textbox({
          label: '称呼:',
          placeholder: '请问怎么称呼，方便我们称呼您',
          key: 'name',
          required: true,
          validateEqual: 'repeat',
        }),
        new Textbox({
          label: '邮箱:',
          placeholder: '留下您的邮箱，方便我们联系您',
          key: 'email',
          required: true,
        }),
        new Textbox({
          label: '您的站点:',
          placeholder: '方便我们更加了解您',
          key: 'website',
        })
      ]);
    }
    return fields;
  }

  comment(comment: CommentEntity) {
    let header;
    if (this.loginService.isLogin) {
      header = this.loginService.getLoginedHeader();
    } else {
      header = this.loginService.headers;
    }
    return this.http.post(this.commentUrl, JSON.stringify(comment), {headers: header}).toPromise().then(
      data => {
        if (data['status'] === 'ok') {
          return true;
        }
        this.message = data['message'];
        return false;
      }
    ).catch(data => {
      if (data.error) {
        this.message = data.error.message;
      }
      return false;
    });
  }

  getcomment() {
    return this.http.get('').toPromise().then(data => {
      this.comments = data['results'] as [CommentEntity];
    });
  }
}



