import {FieldBase} from './field-base';

export class CheckBox extends FieldBase<string> {
  controlType = 'checkbox';
  type: string;
  options: string;

  constructor(options: {} = {}) {
    super(options);
    this.type = options['type'] || '';
    this.options = options['options'] || '';
  }
}



