import {Component, OnInit, Input} from '@angular/core';
import {Router} from '@angular/router';
import {LoginService} from '../../services/login.service';
import {User} from '../../../entity/user.entity';
import {FieldBase, Textbox, CheckBox} from '../../components/dynamic-form/form-field/index';
import {FormGroup} from '@angular/forms';
import {FormService} from '../../services/form.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  // styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  private isLogin = false;
  protected user: User;
  protected error: boolean;
  protected message: string;

  @Input() fields: FieldBase<any>[] = [
    new Textbox({
      label: '用户名:',
      placeholder: '用户名',
      key: 'username',
      type: 'text',
      required: true,
    }),
    new Textbox({
      label: '密 码:',
      placeholder: '密 码',
      key: 'password',
      type: 'password',
      required: true,
      validateEqual: 'repeat',
    }),
    new CheckBox({
      label: ' ',
      key: 'member',
      type: 'checkbox',
      options: [
        {id: '1', text: '记住我'},
      ]
    })
  ];
  private form: FormGroup;

  constructor(private loginService: LoginService, private router: Router, private formService: FormService) {
  }

  ngOnInit() {
    this.form = this.formService.toFormGroup(this.fields);
    this.user = this.loginService.getCacheUser();
    if (!this.user) {
      this.user = new User();
    }
    console.log(this.user);
  }

  login() {
    this.user = this.form.value as User;
    if (this.form.value.member.length) {
      this.loginService.cacheUser(this.user);
    }
    this.loginService.login(this.user).then(
      data => {
        if (data) {
          this.error = false;
          console.log('login ok');
          this.isLogin = true;
          this.router.navigate(['/me']);
        } else {
          this.error = true;
          this.message = this.loginService.message;
        }
      }
    );
  }

}



