import {Component, Input, Output, EventEmitter} from '@angular/core';


@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css'],
})
export class SearchComponent {
  @Output() searchContent = new EventEmitter<Object>();
  @Output() clickContent = new EventEmitter<Object>();
  @Input('results') results = [];
  searchText = '';
  @Input('next') next = '';
  @Input('previous') previous;

  search(searchText: string, page) {
    if (!searchText || searchText === '') {
      return;
    }
    if (page) {
      this.searchContent.emit(this.searchText + 'pageSplit' + page);
      return;
    }
    this.searchContent.emit(this.searchText);
  }
}



