import {BrowserModule} from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {NgModule, CUSTOM_ELEMENTS_SCHEMA} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';

import {AppComponent} from './app.component';
import {TreeComponent} from './charts/tree.component';
import {AppRoutingModule} from './app.routing.module';
import {PeopleService} from './services/people.service';
import {LoginComponent} from './users/login/login.component';
import {LoginService} from './services/login.service';
import {SearchComponent} from './components/search/search.component';
import {IndexComponent} from './index/index.component';
import {ProfileComponent} from './users/profile/profile.component';
import {MeService} from './services/me.service';
import {SharedModule} from './shared/shared.module';
import {FormControlComponent} from './components/dynamic-form/form-control.component';
import {MeComponent} from './users/me/me.component';
import {PeopleManagerComponent} from './users/people-manager/people-manager.component';
import {RegisterComponent} from './users/register/register.component';
import {FormService} from './services/form.service';
import {CheckboxGroupComponent} from './components/dynamic-form/form-field/checkbox.component';
import { FeedbackComponent } from './feedback/feedback.component';
import {CommentService} from './services/comment.service';


@NgModule({
  declarations: [
    AppComponent,
    TreeComponent,
    LoginComponent,
    SearchComponent,
    IndexComponent,
    ProfileComponent,
    FormControlComponent,
    MeComponent,
    PeopleManagerComponent,
    RegisterComponent,
    CheckboxGroupComponent,
    FeedbackComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    BrowserAnimationsModule,
    NgbModule.forRoot(),
    // SharedModule,
    ReactiveFormsModule,
    AppRoutingModule,
  ],
  providers: [PeopleService, LoginService, MeService, FormService, CommentService],
  bootstrap: [AppComponent],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class AppModule {
}



