import {Component, OnInit, Input, ElementRef} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {UserProfile} from '../../../entity/user.entity';
import {MeService} from '../../services/me.service';
import {FieldBase, Textbox, Image, TextArea, DropDown} from '../../components/dynamic-form/form-field/index';
import {ActivatedRoute, Router} from '@angular/router';
import {FormService} from '../../services/form.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  userProfile: UserProfile;

  @Input() fields: FieldBase<any>[] = [
    new Image({
      src: 'assets/imgs/angular2-small.png',
    }),
    new Textbox({
      avatar: 'avatar',
      label: '头像:',
      placeholder: '上传头像',
      type: 'file'
    }),
    new Textbox({
      label: '昵称:',
      placeholder: '昵称',
      key: 'nick',
      required: true,
    }),
    new DropDown({
      label: '性别:',
      placeholder: '性别',
      key: 'sex',
      options: [{key: 'male', 'value': '男'}, {key: 'female', value: '女'}],
    }),
    new Textbox({
      label: '生日:',
      placeholder: '生日',
      key: 'birth',
      type: 'date'
    }),
    new Textbox({
      label: '电话:',
      placeholder: '电话',
      key: 'phone',
    }),
    new Textbox({
      key: 'email',
      type: 'email',
      label: '常用邮箱:',
      placeholder: '常用邮箱',
    }),
    new TextArea({
      key: 'info',
      label: '个人简介:',
      placeholder: '个人简介，最多140字，不能放链接。',
      rows: 3,
    })
  ];

  public form: FormGroup;

  constructor(private meService: MeService, public router: Router, public activeRoute: ActivatedRoute, private elementRef: ElementRef,
              private formService: FormService) {
  }

  ngOnInit() {
    this.form = this.formService.toFormGroup(this.fields);
    this.getProfile();
    this.activeRoute.params.subscribe(
      params => {
        console.log(params);
      }
    );
  }

  getProfile() {
    this.meService.getProfile().then(data => {
        this.userProfile = data as UserProfile;
        this.form.patchValue(data);
      }
    );
  }

  updateProfile(profile: UserProfile) {
    this.meService.updateProfile(profile).then(data => {
        this.userProfile = data as UserProfile;
      }
    );
  }

  save(e) {
    const profile = this.form.value as UserProfile;
    return this.updateProfile(profile);
  }
}



