import {Injectable} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {FieldBase} from '../components/dynamic-form/form-field/index';
import {RepeatValidatorDirective} from '../directive/validator/repeat.validator.directive';

@Injectable()
export class FormService {
  constructor() {
  }

  toFormGroup(fields: FieldBase<any>[]) {
    const group: any = {};

    fields.forEach(field => {
      const validators = [];
      if (field.required) {
        validators.push(Validators.required);
      }
      switch (field.key) {
        case 'email':
          validators.push(Validators.email);
          break;
        case 'repeat':
          validators.push(new RepeatValidatorDirective('password', 'true'));
          break;
      }
      if (field.key === 'email') {
        console.log(validators);
      }
      group[field.key] = new FormControl(field.value || '', validators);
    });
    return new FormGroup(group);
  }
}



